<template>
  <div>
    <h1>게시물 목록</h1>
    <ul>
      <li v-for="post in posts" :key="post.id">
        <h2>{{ post.title }}</h2>
        <p>{{ post.content }}</p>
        <img
          v-if="post.imagePath"
          :src="`/uploads/${post.imagePath}`"
          alt="첨부 이미지"
          width="200"
        />
      </li>
    </ul>
    <button @click.prevent="handleClick">모집 공고 글 쓰기</button>
  </div>
</template>

<script setup>
import { ref } from 'vue';
import axios from 'axios';
import { useRouter } from 'vue-router';
import { useUserStore } from '@/store/SignUp';

const router = useRouter();
const userStore = useUserStore();

const username = ref('');
const password = ref('');

const handleClick = async () => {
  if (userStore.isLoggedIn) {
    if (userStore.isAdmin) {
      router.push({ name: 'PrModRecruitmentNotice' }); // 관리자 권한인 경우 PrModRecruitmentNotice 페이지로 이동
    } else {
      alert('관리자만 접근할 수 있습니다.');
    }
  } else {
    alert('로그인 해주세요.');
    await handleLogin();
  }
};

const handleLogin = async () => {
  try {
    const payload = {
      username: userStore.username,
      password: userStore.password,
    };

    const response = await axios.post('http://localhost:8084/login', payload);

    if (response.data.message) {
      console.log('User logged in successfully');
      userStore.isLoggedIn = true;
      userStore.isAdmin = response.data.grade === 1;
      router.push({ name: 'RecruitmentNotice' });
    } else {
      console.error('Login failed');
      userStore.isLoggedIn = false;
    }
  } catch (error) {
    console.error('Error logging in user:', error);
    userStore.isLoggedIn = false;
  }
};

const fetchPosts = async () => {
  try {
    const response = await axios.get('http://localhost:8084/posts');
    posts.value = response.data;
  } catch (error) {
    console.error('게시물 목록을 가져오는 중 오류 발생:', error);
  }
};
</script>

<style>
/* 스타일이 필요한 경우 추가합니다. */
</style>
